import React, { useEffect, useState } from "react";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";

import "./Jumbotron.css";

const useStyles = makeStyles((theme) => ({
  section:{
    width: '100%',
    overflow: 'hidden',
    position: 'relative',
    zIndex: '1',
  }
}));

export default function Jumbotron(props) {

  const classes = useStyles();

  //Acceptable props

  //type = "splat-toggle"
  //type = "splat-toggle single-toggle"
  //type = "splat-toggle back-row-toggle"

  // It takes a function
  useEffect(
    () => {
      // This gets called after every render, by default
      // (the first one, and every one after that)
      makeItRain();
    },
    [
      // dependencies to watch = leave blank to run once or you will get a stack overflow
    ]
  );

  const makeItRain = function () {

    //clear out everything
    //Get element named rain then inner html to nothing
    //document.getElementById('rain-body').innerHTML = '';

    let increment = 0;
    let drops = "";
    let backDrops = "";

    while (increment < 100) {
      //couple random numbers to use for various randomizations
      //random number between 98 and 1
      let randoHundo = Math.floor(Math.random() * (98 - 1 + 1) + 1);
      //random number between 5 and 2
      let randoFiver = Math.floor(Math.random() * (5 - 2 + 1) + 2);
      //increment
      increment += randoFiver;
      //add in a new raindrop with various randomizations to certain CSS properties
      drops +=
        '<div class="drop" style="left: ' +
        increment +
        "%; bottom: " +
        (randoFiver + randoFiver - 1 + 100) +
        "%; animation-delay: 0." +
        randoHundo +
        "s; animation-duration: 0.5" +
        randoHundo +
        's;"><div class="stem" style="animation-delay: 0.' +
        randoHundo +
        "s; animation-duration: 0.5" +
        randoHundo +
        's;"></div><div class="splat" style="animation-delay: 0.' +
        randoHundo +
        "s; animation-duration: 0.5" +
        randoHundo +
        's;"></div></div>';
      backDrops +=
        '<div class="drop" style="right: ' +
        increment +
        "%; bottom: " +
        (randoFiver + randoFiver - 1 + 100) +
        "%; animation-delay: 0." +
        randoHundo +
        "s; animation-duration: 0.5" +
        randoHundo +
        's;"><div class="stem" style="animation-delay: 0.' +
        randoHundo +
        "s; animation-duration: 0.5" +
        randoHundo +
        's;"></div><div class="splat" style="animation-delay: 0.' +
        randoHundo +
        "s; animation-duration: 0.5" +
        randoHundo +
        's;"></div></div>';
    }

    document.getElementById("rain-front-row").innerHTML = drops;
    document.getElementById("rain-back-row").innerHTML = backDrops;
  };

  return (
    <Box className={classes.section}>
      <div id="rain-body" className={"body " + props.type}>
        <div className="back-row-toggle splat-toggle">
          <div id="rain-front-row" className="rain front-row"></div>
          <div id="rain-back-row" className="rain back-row"></div>
        </div>
      </div>
    </Box>
  );
}
